<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">1、SVG 简介</p>
            <p>
                （1）<span class="tip">SVG</span> 是可缩放矢量图形，是 <span class="tip">W3C XML</span> 的分支语言之一，用于标记可缩放的矢量图形。</p><p>（2）下面举个例子具体说明一下。
            </p>
            <pre class='code'><span class='arrow'>&lt;</span><span class='tag'>svg</span><span class='attr'> version=</span><span class='str'>"1.1"</span><span class='attr'> xmlns=</span><span class='str'>"http://www.w3.org/2000/svg"</span><span class='attr'> xmlns:xlink=</span><span class='str'>"http://www.w3.org/1999/link"</span><span class='attr'> xml:space=</span><span class='str'>"preserve"</span><span class='attr'> class=</span><span class='str'>"circle-load-rect-svg"</span><span class='attr'> width=</span><span class='str'>"300"</span><span class='attr'> height=</span><span class='str'>"200"</span><span class='attr'> viewbox=</span><span class='str'>"0 0 600 400"</span><span class='arrow'>></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class='arrow'>&lt;</span><span class='tag'>polyline</span><span class='attr'> points=</span><span class='str'>"5 5,575 5,575 200,5 200"</span><span class='attr'> class=</span><span class='str'>"g-rect-path"</span><span class='arrow'>/></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class='arrow'>&lt;</span><span class='tag'>polyline</span><span class='attr'> points=</span><span class='str'>"5 5,575 5,575 200,5 200"</span><span class='attr'> class=</span><span class='str'>"g-rect-fill"</span><span class='arrow'>/></span><br><span class='arrow'>&lt;/</span><span class='tag'>svg</span><span class='arrow'>></span></pre>
            <p>解释：</p>
            <p>（a）<span class="code">version</span> 表示 <span class="tip">SVG</span> 的版本，目前有1.0 和 1.1 两种；</p>
            <p>（b）<span class="code">xmlns</span> 是固定值 <span class="code">http://www.w3.org/2000/svg</span></p>
            <p>（c）<span class="code">xmlns:xlink</span> 是固定值 <span class="code">http://www.w3.org/1999/xlink</span></p><p>（d）<span class="code">xml:space</span> 是固定值 <span class="code">preserve</span></p>
            <p>（e）<span class="code">class</span> 类名</p>
            <p>（f）<span class="code">width|height</span> 定义 <span class="tip">SVG</span> 画布的大小</p>
            <p>（g）<span class="code">viewbox</span> 定义了画布上可以显示的区域，当 <span class="code">viewbox</span> 的大小和 <span class="tip">SVG</span> 不同时，<span class="code">viewbox</span> 会在屏幕上缩放至等同 <span class="tip">SVG</span> 的大小</p>
            <p>上述效果展示：（画出一个填充的矩形，默认颜色是黑色）</p>
            <svg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' xml:space='preserve' class='circle-load-rect-svg' width='300' height='200' viewbox='0 0 600 400'><polyline points='5 5, 575 5, 575 200, 5 200' class='g-rect-path'/><polyline points='5 5, 575 5, 575 200, 5 200' class='g-rect-fill'/></svg>
            <p class="minititle">2、基本图形</p>
            <p>
                下面是常用的基本图形的介绍说明：
            </p>
            <p class="text-center">SVG 基本图形</p>
            <table class="table table-hover table-bordered text-center">
                <thead>
                    <tr>
                        <td width="13%">图形</td>
                        <td width="26%">标签名</td>
                        <td width="25%">属性</td>
                        <td>说明</td>
                    </tr>
                </thead>
                <tbody v-for="item in table_42_1" :key="item.name">
                    <tr>
                        <td :rowspan="item.child.length+1">{{item.name}}</td>
                        <td :rowspan="item.child.length+1">{{item.tagName}}</td>
                    </tr>
                    <tr v-for="c in item.child" :key="c.attr">
                        <td width="50">{{c.attr}}</td>
                        <td>{{c.intro}}</td>
                    </tr>
                </tbody>
            </table>
            <p class="minititle">3、path 详细介绍</p>
            <p>使用 <span class="code">path</span> 只需设置很少的点，就能创建平滑流畅的线条。虽然使用 <span class="code">polyline</span> 也可以做到，但是需要创建大量的点（点越多越密集，就越平滑流畅，并且图形不能放大，放大将导致点分散更明显）</p>
            <p>属性 <span class="code">d</span> 的用法：通过“命令+参数”的序列编辑</p>
            <p class="minititle">（1）直线</p>
            <table class="table table-hover table-bordered text-center">
                <thead>
                    <tr>
                        <td width="13%">命令</td>
                        <td width="26%">解释</td>
                        <td width="25%">用法</td>
                        <td>说明</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in table_42_2" :key="item.directive">
                        <td>{{item.directive}}</td>
                        <td>{{item.explain}}</td>
                        <td>{{item.useage}}</td>
                        <td>{{item.intro}}</td>
                    </tr>
                </tbody>
            </table>
            <p class="tip">tip：坐标之间也可用逗号分隔</p>
            <p class="minititle">（2）曲线</p>
            <table class="table table-hover table-bordered text-center">
                <thead>
                    <tr>
                        <td width="13%">命令</td>
                        <td width="26%">解释</td>
                        <td width="25%">用法</td>
                        <td>说明</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in table_42_3" :key="item.directive">
                        <td>{{item.directive}}</td>
                        <td>{{item.explain}}</td>
                        <td>{{item.useage}}</td>
                        <td>{{item.intro}}</td>
                    </tr>
                </tbody>
            </table>
            <p class="minititle">（3）弧形</p>
            <table class="table table-hover table-bordered text-center">
                <thead>
                    <tr>
                        <td width="13%">命令</td>
                        <td width="26%">解释</td>
                        <td width="25%">用法</td>
                        <td>说明</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in table_42_4"
                        :key="item.directive">
                        <td>{{item.directive}}</td>
                        <td>{{item.explain}}</td>
                        <td>{{item.useage}}</td>
                        <td>{{item.intro}}</td>
                    </tr>
                </tbody>
            </table>
            <p class="minititle">（4）填充和边框</p>
            <table class="table table-hover table-bordered text-center">
                <thead>
                    <tr>
                        <td width="30%">属性</td>
                        <td>解释</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in table_42_5"
                        :key="item.name">
                        <td>{{item.name}}</td>
                        <td>{{item.intro}}</td>
                    </tr>
                </tbody>
            </table>
            <p class="tip">提示1：关于 <span class="code">stroke-opacity</span>， 考虑兼容问题，最好不要使用 <span class="code">rgba</span> 属性，透明度单独使用 <span class="code">opacity</span> </p>
            <p class="tip">提示2：关于 <span class="code">stroke-dasharray</span>，可以定义任意数字，会以数字为长度，奇数填充，偶数不填充，如此循环</p>
            <p class="minititle">4、使用 css 样式</p>
            <p>在 <span class="tip">SVG</span> 中使用 <span class="tip">css</span> 样式，语法和其他元素一样，把其中的 <span class="code">background-color</span> 改成 <span class="code">fill</span> ； <span class="code">border</span> 改成 <span class="code">stroke</span></p>
            <p>但是，并非所有的属性都能使用 <span class="tip">css</span> 进行设置。</p>
            <p><span class="code">fill</span>、<span class="code">stroke</span>、<span class="code">stroke-dasharray</span> 等都可以使用 <span class="tip">css</span> 设置，但是渐变和图案等功能不能使用，另外 <span class="code">width</span>、<span class="code">height</span> 以及路径命令也不能。这是因为 <span class="tip">SVG</span> 的属性分为 <span class="code">properties</span> 和其他 <span class="code">attributes</span>，前者可以用 <span class="tip">css</span>，后者不可以。</p>
            <p>更详细的说明和属性可以参考 SVG 规范：<a href="http://www.w3school.com.cn/svg/svg_reference.asp" target="_blank">http://www.w3school.com.cn/svg/svg_reference.asp</a></p>
            <p class="minititle">5、简单渐变介绍</p>
            <p class="minititle">（1）线性渐变</p>
            <p>与内嵌 <span class="tip">css</span> 类似，在 <span class="code">svg</span> 标签内部的 <span class="code">defs</span> 标签内加入 <span class="code">linearGradient</span> 标签，示例如下：</p>
            <svg width="100%" height="120" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <defs>
                        <linearGradient id="Gradient1">
                            <stop class="stop1" offset="0%"></stop>
                            <stop class="stop2" offset="50%"></stop>
                            <stop class="stop3" offset="100%"></stop>
                        </linearGradient>
                        <linearGradient id="Gradient2" x1="0" x2="0" y1="0" y2="1">
                            <stop offset="0%" stop-color="#ff4949"></stop>
                            <stop offset="50%" stop-color="#fd7424" stop-opacity="0.5"></stop>
                            <stop offset="100%" stop-color="#ff4949"></stop>
                        </linearGradient>
                </defs>
                <rect id="rect1" x="60" y="10" rx="15" ry="15" width="100" height="100"></rect>
                <rect x="200" y="10" rx="15" ry="15" width="100" height="100" fill="url(#Gradient2)"></rect>
            </svg>
                <p>上面两个例子分别使用了 <span class="tip">css</span> 样式和直接在元素上写属性两种方法</p>
                代码如下：
                <pre class="code">
<span class="arrow">&lt;</span><span class="tag">svg</span><span class="attr"> width=</span><span class="str">"100%" </span><span class="attr">height=</span><span class="str">"120" </span><span class="attr">version=</span><span class="str">"1.1" </span><span class="attr">xmlns=</span><span class="str">"http://www.w3.org/2000/svg"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">defs</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">linearGradient </span><span class="attr">id=</span><span class="str">"Gradient1"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop </span><span class="attr">class=</span><span class="str">"stop1" </span><span class="attr">offset=</span><span class="str">"0%"</span><span class="arrow">>&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop </span><span class="attr">class=</span><span class="str">"stop2" </span><span class="attr">offset=</span><span class="str">"50%"</span><span class="arrow">>&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop </span><span class="attr">class=</span><span class="str">"stop3" </span><span class="attr">offset=</span><span class="str">"100%"</span><span class="arrow">>&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">linearGradient</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">linearGradient </span><span class="attr">id=</span><span class="str">"Gradient2" </span><span class="attr">x1=</span><span class="str">"0" </span><span class="attr">x2=</span><span class="str">"0" </span><span class="attr">y1=</span><span class="str">"0" </span><span class="attr">y2=</span><span class="str">"1"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop </span><span class="attr">offset=</span><span class="str">"0%" </span><span class="attr">stop-color=</span><span class="str">"#ff4949"</span><span class="arrow">>&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop </span><span class="attr">offset=</span><span class="str">"50%" </span><span class="attr">stop-color=</span><span class="str">"#fd7424" </span><span class="attr">stop-opacity=</span><span class="str">"0.5"</span><span class="arrow">>&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop </span><span class="attr">offset=</span><span class="str">"100%" </span><span class="attr">stop-color=</span><span class="str">"#ff4949"</span><span class="arrow">>&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">linearGradient</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">defs</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">rect </span><span class="attr">id=</span><span class="str">"rect1" </span><span class="attr">x=</span><span class="str">"60" </span><span class="attr">y=</span><span class="str">"10" </span><span class="attr">rx=</span><span class="str">"15" </span><span class="attr">ry=</span><span class="str">"15"</span><span class="attr">width=</span><span class="str">"100" </span><span class="attr">height=</span><span class="str">"100%"</span><span class="arrow">>&lt;/</span><span class="tag">rect</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">rect </span><span class="attr">x=</span><span class="str">"200" </span><span class="attr">y=</span><span class="str">"10" </span><span class="attr">rx=</span><span class="str">"15" </span><span class="attr">ry=</span><span class="str">"15" </span><span class="attr">width=</span><span class="str">"100" </span><span class="attr">height=</span><span class="str">"100" </span><span class="attr">fill=</span><span class="str">"url(#Gradient2)"</span><span class="arrow">>&lt;/</span><span class="tag">rect</span><span class="arrow">></span>
<span class="arrow">&lt;/</span><span class="tag">svg</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">style</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">#rect1</span>&nbsp;<span class="flower">{</span>&nbsp;<span class="fun">fill</span><span class="word">:</span>&nbsp;<span class="fun">url</span><span class="word">(</span><span class="str">#Gradient1</span><span class="word">);</span>&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">.stop1</span>&nbsp;<span class="flower">{</span>&nbsp;<span class="fun">stop-color</span><span class="word">:</span>&nbsp;<span class="key">#4949ff</span><span class="word">;</span>&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">.stop2</span>&nbsp;<span class="flower">{</span>&nbsp;<span class="fun">stop-color</span><span class="word">:</span>&nbsp;<span class="key">#7424fd</span><span class="word">;</span>&nbsp;<span class="fun">stop-opacity</span><span class="word">:</span>&nbsp;<span class="str">0.5</span><span class="word">;</span>&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">.stop3</span>&nbsp;<span class="flower">{</span>&nbsp;<span class="fun">stop-color</span><span class="word">:</span>&nbsp;<span class="key">#4949ff</span><span class="word">;</span>&nbsp;<span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">style</span><span class="arrow">></span></pre>
            <p class="minititle">（2）径向渐变</p>
            <p>径向渐变使用的是 <span class="code">radialGradient</span>，用法和线性渐变一致，但是渐变有几个特有的属性，先来看一个简单的示例：</p>
            <svg width="100%" height="100" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <radialGradient id="RadialGradient1">
                        <stop offset="0%" stop-color="#667eea"></stop>
                        <stop offset="100%" stop-color="#764ba2" stop-opacity="0.6"></stop>
                    </radialGradient>
                    <radialGradient id="RadialGradient2" cx="0.25" cy="0.3" r="1">
                        <stop offset="0%" stop-color="#667eea"></stop>
                        <stop offset="100%" stop-color="#764ba2" stop-opacity="0.6"></stop>
                    </radialGradient>
                </defs>
                <circle cx="50" cy="50" r="40" fill="url(#RadialGradient1)"></circle>
                <circle cx="140" cy="50" r="40" fill="url(#RadialGradient2)"></circle>
            </svg>
            <p>图一没有设置渐变的位置，图二设置在了左上角。设置径向渐变的参数为：<span class="code">cx</span>（渐变边缘位置的 x 坐标）、<span class="code">cy</span>（渐变边缘位置的 y 坐标）、<span class="code">fx</span>（焦点的 x 坐标）、<span class="code">fy</span>（焦点的 y 坐标）、<span class="code">r</span>（渐变的大小）</p>
            <p>上例的代码如下：</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">svg</span>&nbsp;<span class="attr">width=</span><span class="str">"100%"</span>&nbsp;<span class="attr">height=</span><span class="str">"100"</span>&nbsp;<span class="attr">version=</span><span class="str">"1.1"</span>&nbsp;<span class="attr">xmlns=</span><span class="str">"http://www.w3.org/2000/svg"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">defs</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">radialGradient</span>&nbsp;<span class="attr">id=</span><span class="str">"RadialGradient1"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop</span>&nbsp;<span class="attr">offset=</span><span class="str">"0"</span>&nbsp;<span class="attr">stop-color=</span><span class="str">"#667eea"</span><span class="arrow">></span><span class="arrow">&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop</span>&nbsp;<span class="attr">offset=</span><span class="str">"100%"</span>&nbsp;<span class="attr">stop-color=</span><span class="str">"#764ba2"</span>&nbsp;<span class="attr">stop-opacity=</span><span class="str">"0.6"</span><span class="arrow">></span><span class="arrow">&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">radialGradient</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">radialGradient</span>&nbsp;<span class="attr">id=</span><span class="str">"RadialGradient2"</span>&nbsp;<span class="attr">cx=</span><span class="str">0.25</span>&nbsp;<span class="attr">cy=</span><span class="str">"0.3"</span>&nbsp;<span class="attr">r=</span><span class="str">"1"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop</span>&nbsp;<span class="attr">offset=</span><span class="str">"0"</span>&nbsp;<span class="attr">stop-color=</span><span class="str">"#667eea"</span><span class="arrow">></span><span class="arrow">&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">stop</span>&nbsp;<span class="attr">offset=</span><span class="str">"100%"</span>&nbsp;<span class="attr">stop-color=</span><span class="str">"#764ba2"</span>&nbsp;<span class="attr">stop-opacity=</span><span class="str">"0.6"</span><span class="arrow">></span><span class="arrow">&lt;/</span><span class="tag">stop</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">radialGradient</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">defs</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">circle</span>&nbsp;<span class="attr">cx=</span><span class="str">"50"</span>&nbsp;<span class="attr">r=</span><span class="str">"40"</span>&nbsp;<span class="attr">fill=</span><span class="str">"url(#RadialGradient1)"</span><span class="arrow">>&lt;/</span><span class="tag">circle</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">circle</span>&nbsp;<span class="attr">cx=</span><span class="str">"140"</span>&nbsp;<span class="attr">r=</span><span class="str">"40"</span>&nbsp;<span class="attr">fill=</span><span class="str">"url(#RadialGradient2)"</span><span class="arrow">>&lt;/</span><span class="tag">circle</span><span class="arrow">></span><br><span class="arrow">&lt;/</span><span class="tag">svg</span><span class="arrow">></span></pre>
            <p>除了上面两种渐变方式以外，渐变还有一个控制渐变扩散方式的属性，可以给渐变增加更多的变化，属性名为 <span class="code">spreadMethod</span> 其中的参数有 <span class="code"> pad</span>、<span class="code">reflect</span>、<span class="code">repeat</span>，还可以使用 <span class="code">gradientTransform</span> 控制额外的变化。对此有兴趣的可以自己去学习学习。</p>
            <p>下面是渐变扩散方式的一个简单示例（这里不展示代码，想看的就 <span class="tip">F12</span> 吧~）：</p>
            <svg width="100%" height="120" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <radialGradient id="GradientPad"
                                    cx="0.5" cy="0.5" r="0.4" fx="0.75" fy="0.75"
                                    spreadMethod="pad">
                        <stop offset="0%" stop-color="#ff4949"></stop>
                        <stop offset="100%" stop-color="#7424fd" stop-opacity="0.7"></stop>
                    </radialGradient>
                    <radialGradient id="GradientRepeat"
                                    cx="0.5" cy="0.5" r="0.4" fx="0.75" fy="0.75"
                                    spreadMethod="repeat">
                        <stop offset="0%" stop-color="#ff4949"></stop>
                        <stop offset="100%" stop-color="#7424fd" stop-opacity="0.7"></stop>
                    </radialGradient>
                    <radialGradient id="GradientReflect"
                                    cx="0.5" cy="0.5" r="0.4" fx="0.75" fy="0.75"
                                    spreadMethod="reflect">
                        <stop offset="0%" stop-color="#ff4949"></stop>
                        <stop offset="100%" stop-color="#7424fd" stop-opacity="0.7"></stop>
                    </radialGradient>
                </defs>

                <rect x="30" y="10" rx="15" ry="15" width="100" height="100" fill="url(#GradientPad)"></rect>
                <rect x="150" y="10" rx="15" ry="15" width="100" height="100" fill="url(#GradientRepeat)"></rect>
                <rect x="270" y="10" rx="15" ry="15" width="100" height="100" fill="url(#GradientReflect)"></rect>

                <text x="40" y="30" fill="#f9fafc" font-family="sans-serif" font-size="12px">Pad</text>
                <text x="160" y="30" fill="#f9fafc" font-family="sans-serif" font-size="12px">Repeat</text>
                <text x="280" y="30" fill="#f9fafc" font-family="sans-serif" font-size="12px">Reflect</text>

            </svg>

        </section>
    </div>
</template>
